import React from "react";

// icons
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import CheckIcon from '@mui/icons-material/Check';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';

const Todo = (props) => {
    return (
        <>
            <div className="todo">
                <div className="inner-left-todo">
                    {props.stat===true? <CheckIcon className="check icon" onClick={()=>{
                        props.upStatus(props.id)
                    }}/>:<CircleOutlinedIcon className="check icon"onClick={()=>{
                        props.upStatus(props.id)
                    }}/>}
                    <p className={props.stat===true? 'line': ''}>{props.task}</p>
                </div>
                <div className="inner-right-todo">
                    <EditIcon className="edit icon" onClick={()=>{
                        props.upTask(props.id)
                    }}/>
                    <DeleteIcon className="delete icon" onClick={()=>{
                        props.delTask(props.id)
                    }} />
                </div>
            </div>
        </>
    )
}

export default Todo;