import React, { useState, useEffect } from "react";
import Todo from "./todo";

//css
import "./container.css"

//icons
import SaveIcon from '@mui/icons-material/Save';


const Container = () => {
    const [input, setInput] = useState("")
    const [todo, setTodo] = useState([])
    const [editing, setEditing] = useState(false)
    const [editid, setEditid] = useState("")
    let [remain,setRemain] = useState(null)

    //remaining task function
    useEffect(()=>{
        const countFalseStatus = todo.filter(todo => todo.status === false).length;
        setRemain(countFalseStatus)
    },[todo])

    // Load todo items from local storage on component mount
    useEffect(() => {
        const storedTodo = localStorage.getItem("todo");
        if (storedTodo) {
            setTodo(JSON.parse(storedTodo));
        }
    }, []);

    // Update local storage whenever todo items change
    useEffect(() => {
        localStorage.setItem("todo", JSON.stringify(todo));
    }, [todo]);


    //function for change input
    const changeInput = (e) => {
        setInput(e.target.value)
    }

    //function for adding task
    const addTask = () => {
        if (input === "") {
            alert("Please enter task")
        }
        else if (editing) {
            const newTodo = todo.map((val) => {
                if (val.id === editid) {
                    return {
                        id: val.id,
                        status: val.status,
                        task: `${input}`
                    }
                }
                else {
                    return val
                }
            })
            setTodo(newTodo)
            setInput("")
            setEditing(false)
        }
        else {
            const id = new Date().toISOString()
            const todo = {
                id: id,
                status: false,
                task: `${input}`
            }
            setTodo((prevData) => {
                return [...prevData, todo]
            })
            setInput("")
        }
    }

    //function to delete task
    const delTask = (id) => {
        setTodo((oldArray) => {
            return (oldArray.filter((val) => {
                return val.id !== id
            }))
        })
    }

    //function to delete task
    const upStatus = (id) => {
        const newTodo = todo.map((val) => {
            if (val.id === id) {
                return {
                    id: id,
                    status: !val.status,
                    task: val.task
                }
            }
            else {
                return val
            }
        })
        setTodo(newTodo)
    }

    // function to update task
    const upTask = (id) => {
        const data = todo.find((val) => val.id === id)
        setInput(data.task)
        setEditing(true)
        setEditid(data.id)
    }

    // delete all todo's
    const deleteAll = ()=>{
        setTodo([])
    }

    return (
        <>
            <div id="main">
                <div className="container">
                    <div className="title">
                        <h1>To Do List</h1>
                        <img src="./logo.png" alt="" />
                    </div>
                    <div className="todo-form">
                        <input type="text" value={input} onChange={changeInput} />
                        <button onClick={addTask}>{editing ? <SaveIcon className="save" /> : "+"}</button>
                    </div>
                    <div className="todo-container">
                        {todo.map((val, i) => {
                            return <Todo key={i} id={val.id} stat={val.status} task={val.task} delTask={delTask} upStatus={upStatus} upTask={upTask} />
                        })}
                    </div>
                    {todo.length >= 1 ?
                        <>
                            <hr />
                            <div className="add-info">
                                <h4>Task Reamining: <span>{remain}</span> </h4>
                                <button onClick={deleteAll}>Clear All</button>
                            </div>
                        </>
                        :
                        null
                    }
                </div>
            </div>
        </>
    )
}

export default Container;